@charset "UTF-8";
body {
  margin: 0;
  font-family: 'Comfortaa'; }

.component {
  background: #eee;
  height: 100vh;
  /* фолбэк для браузеров, которые не поддерживают пользовательские свойства */
  height: calc(var(--vh, 1vh) * 100); }

.template {
  height: calc(100vh - 100px);
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  overflow: hidden; }

.actions {
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center; }

.dot:nth-child(1) {
  transition: 0.16s transform ease-in-out;
  transform: translate3d(0, 0, 0); }

.dot:nth-child(2) {
  transition: 0.24s transform ease-in-out;
  transform: translate3d(0, 0, 0); }

.dot:nth-child(3) {
  transition: 0.32s transform ease-in-out;
  transform: translate3d(0, 0, 0); }

.dot:nth-child(4) {
  transition: 0.4s transform ease-in-out;
  transform: translate3d(0, 0, 0); }

.dot:nth-child(5) {
  transition: 0.48s transform ease-in-out;
  transform: translate3d(0, 0, 0); }

.dot:nth-child(6) {
  transition: 0.56s transform ease-in-out;
  transform: translate3d(0, 0, 0); }

.dot:nth-child(7) {
  transition: 0.64s transform ease-in-out;
  transform: translate3d(0, 0, 0); }

.dot:nth-child(8) {
