.dot:nth-child(10) {
  transition: 0.88s transform ease-in-out;
  transform: translate3d(0, 0, 0); }

.dot:nth-child(11) {
  transition: 0.92s transform ease-in-out;
  transform: translate3d(0, 0, 0); }

