.dot:nth-child(18) {
  transition: 1.2s transform ease-in-out;
  transform: translate3d(0, 0, 0); }

.dot:nth-child(19) {
  transition: 1.24s transform ease-in-out;
  transform: translate3d(0, 0, 0); }

.dot:nth-child(20) {
  transition: 1.28s transform ease-in-out;
  transform: translate3d(0, 0, 0); }

.dot {
  height: 20px;
  width: 20px;
  background: #000;
  border-radius: 50%;
  will-change: transform;
  transition: .65s transform ease;
  position: absolute; }

.device-info {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #eff8fa;
  text-align: center;
  display: none;
  align-items: center;
  padding: 0 5px; }

@media screen and (orientation: portrait) {
  .body {
    overflow: hidden; }
  .device-info {
    display: flex;
    justify-content: center; }
  .component {
    display: none; } }
